.el-scrollbar__bar.is-vertical{width:0px;top:2px}.el-scrollbar__bar.is-vertical>div{width:100%}
.el-scrollbar__bar.is-horizontal{height:6px;left:2px}
.el-scrollbar__bar.is-horizontal>div{height:100%}
.el-scrollbar__wrap{overflow:hidden;height:40px}
